import {FC} from "react";
import "./SectionPage.scss";
import Advertisement from "../../advertisement";
import FeedCard from "../../feedCard";
import Recommended from "../../recommended";
import ShowMoreButton from "../../showMoreButton";

interface SectionPageProps {
  heading: string;
}

const SectionPage: FC<SectionPageProps> = ({heading}) => {
  return (
    <section className={'sectionPage'}>
      <h1 className={'sectionPage__heading'}>{heading}</h1>
      <section className={'sectionPage__mainContent mainContent'}>
        <section className={'mainContent__asideCardsList asideCardsList'}>
          <FeedCard type={'full'}
                    heading={'       '}/>
          <FeedCard type={'half'}
                    heading={'          4  '}/>
          <FeedCard type={'half'}
                    heading={'          4  '}/>
          <FeedCard type={'half'}
                    heading={'          4  '}/>
        </section>
        <aside className={'mainContent__asideAds'}>
          <Advertisement/>
          <Advertisement/>
          <Advertisement/>
        </aside>
      </section>
      <section className={'sectionPage__additionalContent additionalContent'}>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
        <FeedCard type={'half'}
                  heading={'          4  '}/>
      </section>
      <section className={'sectionPage__recommendedContent recommendedContent'}>
        <Recommended heading={''}/>
      </section>
      <section className={'sectionPage__subContent subContent'}>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <Advertisement/>
        <ShowMoreButton/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
        <FeedCard type={'half'} size={'small'} heading={'    '}/>
      </section>
    </section>
  )
}

export default SectionPage;